@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

html {
  scroll-behavior: smooth;
}

header {
  width: 100%;
  padding: 15px 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  transition: 0.5s ease;
  z-index: 9999;
}

header.sticky {
  background: rgba(0, 0, 0, 0.7);
}

header .brand {
  color: #fff;
  font-size: 28px;
  font-weight: 700;
  text-decoration: none;
}

header .navigation {
  position: relative;
}

header .navigation a {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  margin-left: 30px;
}

@media screen and (max-width: 1024px) {
  header {
    justify-content: space-around;
  }
  header .brand {
    font-size: 26px;
    margin-right: 70px;
  }
  header .navigation a {
    margin-left: 24px;
  }
}

@media screen and (max-width: 768px) {
  header .brand {
    font-size: 24px;
    margin: 0 auto;
  }

  header .navigation {
    display: none;
  }

  header .navigation.active {
    z-index: 888;
    position: fixed;
    background: #fff;
    opacity: 1;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  header .navigation a {
    color: #000;
    font-size: 20px;
    margin: 30px 0;
  }

  .menu-btn {
    position: absolute;
    background: url(/portfolio/static/media/menu.32a31f8f.png) no-repeat;
    background-size: 30px;
    background-position: center;
    width: 33px;
    height: 33px;
    right: 0;
    margin: 0 20px;
    cursor: pointer;
  }

  .menu-btn.active {
    background: url(/portfolio/static/media/close.8d5c0be6.png) no-repeat;
    background-size: 25px;
    background-position: center;
    -webkit-filter: invert(1);
            filter: invert(1);
    z-index: 999;
  }
}

.contact {
  padding: 100px 200px;
  background: url(/portfolio/static/media/contactBg1.633df3bb.jpg) no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: bottom;
}

.contact .title2 {
  display: flex;
  justify-content: center;
  margin-bottom: 60px;
}

.contact .content {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.contact .content .box {
  text-align: center;
  margin-bottom: 40px;
}

.contact .content .box a {
  text-decoration: none;
  color: #000;
}

.icon {
  -webkit-transform: scale(2);
          transform: scale(2);
  margin-bottom: 15px;
}

@media screen and (max-width: 1024px) {
  .contact .title2 {
    font-size: 12px;
  }
}

@media screen and (max-width: 768px) {
  .contact {
    padding: 100px 50px;
  }

  .icon {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
    margin-bottom: 15px;
  }

  .contact .content .box h3,
  .contact .content .box span {
    font-size: 14px;
  }
}

.home {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 200px;
  position: relative;
  background: url(/portfolio/static/media/cool-background.a6646348.png) no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}

.home .content {
  text-align: center;
  max-width: 800px;
}

.home .content h2 {
  color: #000;
  font-size: 40px;
  font-weight: 500;
  -webkit-animation-name: homeTitle;
          animation-name: homeTitle;
  -webkit-animation-duration: 1.4s;
          animation-duration: 1.4s;
}

.home .content h2 span {
  font-size: 45px;
  font-weight: 700;
  text-shadow: 2.4px 2.2px 1.4px #fff;
  -webkit-animation-name: homeTitle;
          animation-name: homeTitle;
  -webkit-animation-duration: 2.8s;
          animation-duration: 2.8s;
}

@-webkit-keyframes homeTitle {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes homeTitle {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 1024px) {
  .home .content h2 {
    font-size: 34px;
  }

  .home .content h2 span {
    font-size: 39px;
  }
}

@media screen and (max-width: 768px) {
  .home {
    padding: 50px 100px;
  }

  .home .content h2 {
    font-size: 25px;
  }

  .home .content h2 span {
    font-size: 30px;
  }
}

.profile {
  padding: 100px 200px;
}

.profile .content {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  margin-top: 20px;
}

.profile .content .content-title {
  font-size: 22px;
  margin-bottom: 40px;
}

.profile .content .content-body {
  font-size: 18px;
  margin-bottom: 60px;
  line-height: 2em;
}

.profile .content .content-logo {
  display: flex;
  justify-content: center;
}

.profile .content .content-logo img {
  width: 80px;
  margin: 0px 30px 30px;
  transition: 0.3s ease-in-out;
}

.profile .content .content-logo img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media screen and (max-width: 1024px) {
  .profile .content .content-title {
    font-size: 20px;
    margin-bottom: 35px;
  }

  .profile .content .content-body {
    font-size: 16px;
    line-height: 2em;
  }

  .profile .content .content-logo img {
    width: 60px;
    margin: 0px 25px 30px;
  }
}

@media screen and (max-width: 768px) {
  .profile {
    padding: 100px 50px;
  }

  .profile .content .content-title {
    font-size: 18px;
    margin-bottom: 35px;
  }

  .profile .content .content-body {
    padding: 0 28px;
    font-size: 14px;
    line-height: 1.6em;
    margin-bottom: 50px;
  }

  .profile .content .content-logo img {
    width: 40px;
    margin: 0px 20px 30px;
  }
}

.project {
  padding: 100px 200px;
  background: #ababab;
}

.project .content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.project .content .card {
  width: 340px;
  margin: 0 18px 50px;
}

.project .content .card .card-img {
  background: #fff;
  width: 100%;
  height: 260px;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 3.6px 3.5px 6.2px #333;
}

.project .content .card .card-img img {
  padding: 33px;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 330px;
  height: 200px;
  object-fit: contain;
}

.project .content .card .card-desc {
  position: relative;
  top: 67%;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.project .content .card .card-desc h2 {
  margin-top: 10px;
  font-size: 16px;
  font-weight: 600;
}

.project .content .card .card-desc span {
  margin-top: 6px;
  font-size: 14px;
}

@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 768px) {
  .project {
    padding: 100px 50px;
  }
}

.title {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title h2 {
  position: relative;
  color: #00adad;
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 60px;
  text-shadow: 1.8px 1.6px 1.2px #000000;
}

.title h2:before {
  content: "- ";
}

.title h2:after {
  content: " -";
}

@media screen and (max-width: 1024px) {
  .title h2 {
    font-size: 36px;
    margin-bottom: 60px;
  }
}

@media screen and (max-width: 768px) {
  .title h2 {
    font-size: 30px;
    margin-bottom: 60px;
  }
}

.footer {
  background: #000016;
  color: #fff;
  text-align: center;
  padding: 30px;
}

.footer p {
  font-size: 12px;
}

